import { instantiate } from "cc";
import { Node } from "cc";


/*
    Ui 列表节点条目生成管理

    复用条目节点，不足的生成。多余的隐藏。

*/ 
export class UiListItemGenMgr {

    // 生成父节点
    public root : Node ; 

    // 预制
    public pf : Node ; 

    // 列表
    public list = new Array< Node >() ; 

    public init( node : Node ) {

        this.root = node ; 

        this.pf = this.root.children[ 0 ] ; 

        // 预先加入一个。利用好预制的条目
        this.list.push( this.pf ) ; 

    }

    // 设置条目总数
    public setItemCount( n : number ) {

        let diff = n - this.list.length ; 
        for( let i = 0 ; i < diff ; ++i ){

            let node = instantiate( this.pf ) ;
            node.setParent( this.pf.parent ) ;
            node.active = true ; 
            this.list.push( node ) ;

        } // end for

        // 不显示多余的
        for( let i = this.list.length + diff ; i < this.list.length ; ++i ) {
            this.list[ i ].active = false ; 
        }

    }

}