import { AudioClip, AudioSource, Node, resources, sys, tween } from "cc";

/*
    音效播放控制
    从 resources 中读取

    如果不用对象池，在微信小游戏 微信开发者工具，会出现 warning：Too many (200) InnerAudioContext
*/ 
export class Sound {

    public enabled = true ; 
    
    public constructor() {

        let node = new Node ;
        this.audioSrc = node.addComponent( AudioSource ) ;

    }

    public play( key : string , volume : number = 1.0) {

        if( this.enabled == false ) return ;

        resources.load( key , AudioClip , (err, audio) =>
        { 
            this.audioSrc.playOneShot( audio , volume ) ; 
        } );

    }

    public audioSrc : AudioSource = null ; 

}

