import { ResolutionPolicy, game, screen, view } from "cc";


// 屏幕适配 分辨率策略
export class ScreenAdaptResolutionPolicy {

    /*
        ratio 宽高比

        当前屏幕是：
            长屏幕时，固定宽，延伸高。
            非长屏幕，iPad，固定高，延伸宽。

        适用范围：
            竖屏游戏，横屏游戏

    */ 
    public static exeRatio( ratio : number = 720 / 1280 ) {

        // view.getVisibleSize
        
        let size = screen.resolution ;
        let ratio2 = size.width / size.height ;
        if( ratio >= ratio2 ) {
            // 长屏幕
            view.setResolutionPolicy( ResolutionPolicy.FIXED_WIDTH ) ;
            // console.log( "固定宽" ) ;
        }
        else {
            view.setResolutionPolicy( ResolutionPolicy.FIXED_HEIGHT ) ;
            // console.log( "固定高" ) ;
        }
        
        // console.log( "screen.resolution：" + screen.resolution.width + "," + screen.resolution.height ) ;
        // console.log( "screen.windowSize：" + screen.windowSize.width + "," + screen.windowSize.height ) ;

    }

    public static exeDesignResolution() {

        let size = view.getDesignResolutionSize() ;
        this.exeRatio( size.width / size.height ) ;
    }

}