import { Node, Vec2 } from "cc";
import { BagArticle } from "../Article/BagArticle";

// 背包单元格
export class UiBagCell {

    // 是否是可用的，解锁的
    public unlocked = false ; 

    // 坐标
    public coord : Vec2 ; 

    // 占用的物品
    public article: BagArticle = null ; 


    public root : Node ; 

    public bg : Node ; 

    // 可放置提示
    public hintPlaceable : Node ; 

    // 可被扩展的
    public hintExpandable : Node ; 

    public init( root : Node ) {

        this.root = root ; 

        this.bg = root.getChildByPath( "bg" ) ;

        this.hintPlaceable = root.getChildByPath( "HintPlaceable" ) ;
        this.hintPlaceable.active = false ; 

        this.hintExpandable = root.getChildByPath( "HintExpandable" ) ;
        this.hintExpandable.active = false ; 

    }

}