import { Node, Sprite, Tween, tween } from "cc";



// 条。根节点本身就是 bar。
export class UiBar2 {

    public root : Node ; 

    public bar : Sprite ; 

    public init( root : Node ) {
        
        this.root = root ;
        this.bar = this.root.getComponent( Sprite ) ;

    }

    // 设置进度 0-1的值
    public setProgress( val : number ) {
        this.bar.fillRange = val ;
    }

    // 动画显示
    public anim( val : number , duration : number ) {

        Tween.stopAllByTarget( this.bar ) ; 
        tween( this.bar ).to( duration , { fillRange : val } ).start() ;

    }

}