import { Node } from "cc";
import { Role } from "../Role/Base/Role";
import { Life } from "../Role/Base/Life";
import { AttackCrit, AttackLifeStealing } from "../Tower/Entity/AttackCom";
import { MoveCom } from "../Role/Move/MoveCom";
import { CfgBulletItem } from "../../../../Common/Cfg/CfgBullet";
import { Buff } from "../Buff/Buff";
import { PathMove } from "../Ai/PathMove";

export class Bullet {

    public id : string ; 

    public cfg : CfgBulletItem = null ; 

    // 表现
    public view = new BulletView() ; 

    // 用于控制子弹实体的死亡
    public life = new Life() ; 

    // 生命周期
    public Lifecycle = 0 ; 

    // 伤害
    public damge = 0 ; 

    // 暴击
    public crit : AttackCrit = null ;

    // 吸血
    public lifeStealing : AttackLifeStealing = null ; 

    // 移动
    public move = new MoveCom() ; 
    // 路径移动
    public pathMove : PathMove = null ; 

    // 攻击目标
    public target = new BulletTarget() ; 

    // 穿透 个数
    public passCount = 0 ; 

    // 弹射
    public bounce : BulletBounce = null ;
  
    // 击中后添加的 Buff
    public buffIds = new Array< string >() ; 
    
    // 击中后添加的 Buff
    public buffs = new Array< Buff >() ; 

    // 发送者
    public sender : Sender = null ; 

}

// 子弹运动类型
export enum BulletMoveType {

    None = "None", 

    // 直线
    Linear = "Linear" ,

    // 追踪
    Chase = "Chase" , 

    // 回旋
    Boomerang = "Boomerang" , 

}

export class BulletTarget {

    // 攻击目标
    public targetRole : Role = null ;

    // 运动类型
    public moveType = BulletMoveType.Linear ; 

}

// 子弹反弹
export class BulletBounce {

    public bounceRectEnemy = 0 ; 

    // 边框弹射 次数
    public bounceRect = 0 ; 

    // 敌人反弹 次数
    public bounceEnemy = 0 ; 

    // 敌人间弹射 次数
    public bounceBetweenEnemies = new BulletBounceBetweenEnemies() ; 


}

// 敌人间反弹
export class BulletBounceBetweenEnemies {

    // 弹射次数
    public count = 0 ; 

    // 弹射过的记录
    public record = new Set< Role >() ; 

}

export class BulletView {

    public root : Node ;

    public rotationType = BulletRotationType.None ; 

}

// 子弹运动旋转类型
export enum BulletRotationType {

    None = "None" , 

    // 根据飞行方向旋转
    Direction = "Direction" , 

    // 不断旋转
    Forever = "Forever" , 

}

// 发送者
export class Sender {

    // 发送者ID
    public id : string ; 

}