import { RoleMgr } from "../../1.Base/Role/Base/RoleMgr";
import { LevelData } from "./CfgLevelWaveMgr";
import { WaveCtrl } from "./WaveCtrl";
import { WaveSettle } from "./WaveSettle";


// 关卡级的波控制器。控制波的集合
export class LevelCtrl {

    public static ins : LevelCtrl = null ; 

    // 关卡数据
    public levelData : LevelData = null ; 

    // 当前运行到的波索引。从 1 开始
    public currentWaveIdx = 1 ; 

    // 开始
    public start() {

        this.startWave() ; 

    }

    public reset() {
        this.currentWaveIdx = 1 ; 
    }

    // 启动一波
    public startWave() {

        let wave = this.levelData.waves[ this.currentWaveIdx ] ; 

        WaveCtrl.ins.data = wave ; 
        WaveCtrl.ins.onComplete = this.onCompleted_Wave.bind( this ) ; 
        WaveCtrl.ins.start() ; 

        this.state = 1 ; 

    }

    // 一波控制出兵完成
    private onCompleted_Wave() {

        this.currentWaveIdx++ ; 
        
        this.state = 2 ; 

    }

    public update() {

        switch( this.state ) {

            case 1 : {
                // 执行出兵控制
                WaveCtrl.ins.update() ; 
            }
            break ; 

            case 2 : {
                // 检测一波的怪是否打完
                this.updateWaveEnemy() ; 
            }
            break ; 

        }

    }

    private updateWaveEnemy() {

        if( RoleMgr.ins.allEnemies.length > 0 ) return ; 

        this.state = 0 ; 

        // 清完了一波的怪
        this.onClearWaveAllEnemies() ; 

    }

    // 清完一波的所有敌人
    private onClearWaveAllEnemies() {

        WaveSettle.exe() ; 

    }

    private state = 0 ; 

}

