import { Vec3 } from "cc";


// 移动组件
export class MoveCom
{
    public enabled : boolean  = true;

    // 锁定计数器。
    public lockCount : number = 0 ; 

    // 移动速率。单位：距离/每帧
    public speed = 1;
    
    // 是否能推
    // public bool shoveEnable = true;

    // 限制计数。为0时可移动，每帧递减。
    // 用来攻击时阻塞移动
    // public int limitCount = 0;

    // 以下临时计算变量
    // ————————————————————————————————————

    // 移动方向。值归一化
    public direction = new Vec3() ; 

    /*
        移动偏移量。移动量 / 每帧

        移动系统用这个变量实现移动。
        避免每帧都计算步长（方向 * 速度），简化移动系统，移动系统只需要做一次加法。
        
    */
    public offset = new Vec3() ; 

    // 每帧移动后的位置
    public positionTest = new Vec3() ;

}