

export class DateTimeUtil {

    // 日期部分是否相等
    public static dateEquals( dt1 : Date , dt2 : Date ) : boolean {

        let y1 = dt1.getFullYear() ;
        let y2 = dt2.getFullYear() ; 
        let m1 = dt1.getMonth()  ;
        let m2 = dt2.getMonth() ; 
        let d1 = dt1.getDate() ; 
        let d2 = dt2.getDate()

        let b = y1 == y2 &&
            m1 == m2 && 
            d1 == d2 ; 

        return b ; 

    }


    public static toString( dt : Date ) {

        /*
            toLocaleString 不能用的原因，在不同平台会生成不同格式的字符串。
            比如 cocos 编辑器的字符串就有中文，并且不能用 parse 解析回来。

            iOS 规定日期分割要用 / 不能用 - ，否者会返回 Invalid Date 
            参考：https://blog.csdn.net/qq_38974163/article/details/129039155
        */

        let str = dt.getFullYear() + "/" + 
            ( dt.getMonth() + 1 ) + "/" + 
            dt.getDate() + " " + 
            dt.getHours() + ":" + 
            dt.getMinutes() + ":" + 
            dt.getSeconds() ;

        return str ; 

    }
    
    public static toDateString( dt : Date ) : string {

        // "2023-12-18 14:51:28" 
        let str = dt.getFullYear() + "/" + 
            ( dt.getMonth() + 1 ) + "/" + 
            dt.getDate() ;
            // dt.getUTCHours() + ":" + 
            // dt.getUTCMinutes() + ":" + 
            // dt.getUTCSeconds() ;

        return str ; 

    } 

}