import { Node } from "cc";

// 背景 用于表达品质
export class UiBgQuality {

    public root : Node ; 

    public init( node : Node ) {

        this.root = node ; 

    }

    // 品质 从1开始。1最低级
    public show( val : number ) {

        let children = this.root.children ; 
        for( let child of children ) {
            child.active = false ; 
        } // end for

        let idx = val - 1 ; 
        if( children.length <= idx ) {
            // 超出下标
            console.log( "UiBgQuality.show 超出下标" ) ;
            return ; 
        }
        children[ idx ].active = true ; 

    }

}