import { Input, Label, Node, Prefab, Sprite, UITransform, Vec3, instantiate, resources } from "cc";
import { OsJudge } from "../../../../SL/Cocos/Misc/OsJudge";


// 更多游戏
export class UiMoreGames {

    // 创建 resources 载入机制
    public static createRes( path : string , cb : ( ui : UiMoreGames )=>void ) {

        resources.load( path , Prefab , ( err , data )=>{

            let root = instantiate( data ) ; 

            let ui = new UiMoreGames() ;
            ui.init( root ) ;

            cb( ui ) ;

        } ) ;

    }

    public root : Node ;

    // 关闭按钮
    public btnClose : Node ; 

    // 条目项
    public items = new Array< UiMoreGamesItem >() ; 

    // 预设的图标
    private icons : Node ; 

    // 条目预制
    private pfItem : Node ; 

    // 关闭处理器。决定如何关闭UI
    public closeHandler : ()=>void = null ; 

    // 点击条目
    public onClickItem : ()=>void = null ;

    public init( node : Node ) {

        this.root = node ;

        this.root.position = Vec3.ZERO ; 

        // 点击后销毁
        this.closeHandler = ()=>{ this.root.destroy() ; } ;

        // 关闭按钮
        this.btnClose = this.root.getChildByPath("btnClose") ; 
        this.btnClose.on( Input.EventType.TOUCH_END , ()=>{
            this.closeHandler() ;
        } , this ) ;

        // 遮罩
        let mask = this.root.getChildByPath("mask") ; 
        mask.getComponent( UITransform ).setContentSize( 2000 , 2000 ) ;
        mask.on( Input.EventType.TOUCH_END , ()=>{
            this.closeHandler() ;
        } , this ) ;
        
        this.icons = this.root.getChildByPath( "Icons" ) ;
        this.icons.active = false ; 

        this.initItems() ; 

    }

    private initItems() {

        let itemsNode = this.root.getChildByPath( "Items" ) ; 
        let c = itemsNode.children ;

        // 删除多余的
        for( let i = c.length - 1 ; i > 0 ; --i ) {
            c[ i ].destroy() ;
        }

        this.pfItem = c[ 0 ] ;
        this.pfItem.active = false ;
        
    }

    public refresh( cfg : UiMoreGamesCfg ) {

        this.clear() ; 

        for( let i = 0 ;i < cfg.items.length ; ++i ) {
            let cfgItem = cfg.items[ i ] ;

            // 本机APP跳过
            if( cfg.thisAppId == cfgItem.appId ) continue ;

            this.addItem( cfgItem ) ;

        } // end for

    }

    // 清除
    public clear() {

        for( let i = 0 ; i < this.items.length ; ++i ) {
            this.items[ i ].root.destroy() ;
        }

        this.items.splice( 0 , this.items.length ) ;

    }

    public addItem( cfgItem : UiMoreGamesCfgItem ) {

        let parent = this.pfItem.parent ; 

        let node = instantiate( this.pfItem ) ;
        node.active = true ; 
        node.setParent( parent ) ;
        let uiItem = new UiMoreGamesItem() ; 
        this.items.push( uiItem ) ;
        uiItem.init( node ) ;

        // 名字
        uiItem.name.string = cfgItem.name ;

        // 图标
        let spr = this.icons.children.find( ( e )=>{ return e.name == cfgItem.iconId ; } ).getComponent( Sprite ) ;
        uiItem.icon.spriteFrame = spr.spriteFrame ;

        // 点击
        node.on( Input.EventType.TOUCH_END , ()=>{

            this.navigateToMiniProgram( cfgItem.appId ) ;

            if( this.onClickItem != null ){ this.onClickItem() ; }
            
        } ) ;

    }

    private navigateToMiniProgram( appId ) {

        // // @ts-ignore
        if( OsJudge.isWx() == false ) return ;

        // @ts-ignore
        wx.navigateToMiniProgram({
            appId: appId ,
            path: '',
            extraData: null ,
            envVersion: '',
            success(res) {
                // 打开成功
                console.log( "跳转成功" ) ;
            }
        })

    }

}

export class UiMoreGamesItem {

    public root : Node ;

    // 图标
    public icon : Sprite ; 

    // 名称
    public name : Label ;

    public init( node : Node ) {

        this.root = node ; 

        this.icon = this.root.getChildByPath( "Icon/val" ).getComponent( Sprite ) ;

        this.name = this.root.getChildByPath( "Name" ).getComponent( Label ) ;

    }

    

}

// 配置
export class UiMoreGamesCfg {
    
    public static create( thisAppId : string ) : UiMoreGamesCfg {

        let ins = new UiMoreGamesCfg() ;

        let item = new UiMoreGamesCfgItem() ;
        ins.items.push( item ) ;
        item.appId = "wx1eead536288187e6" ;
        item.iconId = "BlockBlast" ;
        item.name = "方块爆破" ; 

        // item = new UiMoreGamesCfgItem() ;
        // ins.items.push( item ) ;
        // item.appId = "wxe5ceb461d21a27dc" ;
        // item.iconId = "PopStar" ;
        // item.name = "消灭星星" ; 

        item = new UiMoreGamesCfgItem() ;
        ins.items.push( item ) ;
        item.appId = "wxdaca7b7b61c07cd6" ;
        item.iconId = "Snake" ;
        item.name = "拖尾乱斗" ; 

        ins.thisAppId = "wx1eead536288187e6" ;
        ins.thisAppId = thisAppId ;

        return ins ; 

    }

    // 本APP ID
    public thisAppId : string ;

    // 所有 APP
    public items = new Array<UiMoreGamesCfgItem>() ;

}

export class UiMoreGamesCfgItem {

    // 应用ID
    public appId : string ; 

    // 图标ID
    public iconId : string ;

    // 名称
    public name : string ;

}