import { CfgBuffItem } from "../../../../Common/Cfg/CfgBuff";
import { Sender } from "../Bullet/Bullet";
import { CurrentMax } from "../Support/SimpleStruct/CurrentMax";
import { Affix } from "./Affix";


/*

    Buff 的定义：
        有作用时间、触发间隔 的 词缀。
        凡是词缀出现的地方，都可以用Buff替代。

    Buff 使用的场合：
        挂在 Player 上。特性，主要影响Tower。
        挂在 Role 上。
        
        子弹天生附带Buff。在子弹配置表中直接配置。
        3选动态加上去的Buff。Player Buff。

*/

// Buff 基类
export class Buff {

    public cfg : CfgBuffItem = null ; 

    // 图标ID
    public iconId : string ; 

    // 词缀
    public affix : Affix = null ; 

    // 持续时间
    public duration : CurrentMax = null ; 

    // 触发间隔
    public interval : CurrentMax = null ; 

    // 影响指定类型的实体
    // public specified : Set< string > = null ; 

    // 是否是天生的
    public isNative = false ; 

    // 超出基本需求的额外数据
    public extra : any ; 

    public sender : Sender ; 
}

// 作用于角色身上的Buff
export class BuffRole extends Buff {

    // 作用周期
    public interval : CurrentMax = null ; 

}

