import { find } from "cc";
import { Bullet } from "../../1.Base/Bullet/Bullet";
import { BulletMgr } from "../../1.Base/Bullet/BulletMgr";
import { RoleMgr } from "../../1.Base/Role/Base/RoleMgr";
import { WaveCtrl } from "../Level/WaveCtrl";

// 暂停 恢复
export class GamePause {

    // 暂停
    public static pause() {

        // 停止逻辑帧
        find( "Entry" ).active = false ; 

    }

    // 恢复
    public static resume() {

        find( "Entry" ).active = true ; 

    }

}