

import { Music } from "../Audio/Music";
import { AdBase } from "./AdBase";
import { AdRewardVideoFailHandler } from "./Misc/AdRewardVideoFailHandler";
import { director, tween } from "cc";

// 广告 抖音
export class AdTt extends AdBase {

    public override init() {

        /*
            抖音的开发者工具 不支持广告播放，会报错，终止游戏运行。

            兼容开发者工具运行。
        */

        // @ts-ignore
        let info = tt.getSystemInfoSync();
        if( info[ "platform" ] == "devtools" ) {
            return ; 
        }

        this.banner = new AdBanner() ;
        this.banner.init() ;

        this.interstitial = new AdInterstitial() ;
        this.interstitial.init() ;

        this.rewardedVideo = new AdRewardedVideo() ;
        this.rewardedVideo.init() ; 

    }

    public override bannerShow() {
        
        // this.banner?.show() ; 

    }

    public override bannerHide() {
        
        // this.banner?.hide() ; 

    }

    public override interstitialShow() {

        this.interstitial?.show() ;
    }

    // onComplete 变量名不能为 ok 否者编译出错
    public override rewardShow( 
        onSuccess : ()=>void , 
        onCancel : ()=>void = null , 
        onError : ( err : string )=>void = null , 
         ) {

        if( this.rewardedVideo == null ) {

            onSuccess?.() ; 
            return ; 
        }

        let duration = 0.5 ; 

        this.rewardedVideo.onSuccess = ()=>{

            this.resumeBgm() ; 

            tween( this ).delay( duration ).call( ()=>{ 

                onSuccess() ; 

            } ).start() ;

        } ;
        
        this.rewardedVideo.onFailed = ( err : string )=>{ 

            this.resumeBgm() ; 
            
            AdRewardVideoFailHandler.exe( err ) ; 

            onError?.( err ) ;

        } ;

        this.rewardedVideo.onCancel = ()=>{
            onCancel?.() ; 
        } ;

        this.rewardedVideo.show() ; 

    }

    // 恢复音乐
    private resumeBgm() {

        let name = director.getScene().name ; 
        // if( name == "Game" ) {
        //     Music.ins.bg1() ; 
        // }
        // else if( name == "Home" ) {
        //     Bgm.ins.home() ; 
        // }
        Music.ins.bg1() ; 
    }

    public banner : AdBanner ; 
    public interstitial : AdInterstitial ;
    public rewardedVideo : AdRewardedVideo ; 

}


// Banner 广告
class AdBanner {

    public init() {
        
        /*
            参考：
            https://developer.open-douyin.com/docs/resource/zh-CN/mini-game/develop/api/ads/tt-create-banner-ad
            https://developer.open-douyin.com/docs/resource/zh-CN/mini-game/develop/api/ads/banner-ad/banner-ad-on-resize


            当广告没有填充时，会显示了一个无法改变位置的在底部的广告。
            参考：
            https://developer.open-douyin.com/docs/resource/zh-CN/mini-game/develop/api/ads/banner-ad/banner-ad

        */ 

        // @ts-ignore
        const { windowWidth, windowHeight } = tt.getSystemInfoSync();
        const targetBannerAdWidth = 50 ;

        // @ts-ignore
        let bannerAd = tt.createBannerAd({
            adUnitId: '1fl16h0lomd647n0o0',
            adIntervals: 60 , 
            style: {
                width: targetBannerAdWidth,
                top: windowHeight - (targetBannerAdWidth / 16) * 9 , // 根据系统约定尺寸计算出广告高度
                // top: windowHeight - (targetBannerAdWidth / windowHeight ) * windowWidth , // 根据系统约定尺寸计算出广告高度
            },

        })
        this.bannerAd = bannerAd ;

        // 也可以手动修改属性以调整广告尺寸
        bannerAd.style.left = (windowWidth - targetBannerAdWidth) / 2;

        // 尺寸调整时会触发回调，通过回调拿到的广告真实宽高再进行定位适配处理
        // 注意：如果在回调里再次调整尺寸，要确保不要触发死循环！！！
        bannerAd.onResize((size) => {

            console.log(size.width, size.height);
            // bannerAd.style.top = windowHeight - size.height;
            // bannerAd.style.left = (windowWidth - size.width) / 2;

        });



        bannerAd.onError(err => {
            console.log( "banner 广告错误:" + err)
        })
        
        bannerAd.onLoad(() => {
            console.log('banner 广告加载成功') ; 

            this.isLoaded = true ; 

            // 直接显示
            this.show() ; 

        })



    }

    public show() {
        
        if( this.isLoaded ) {
            this.bannerAd?.show() ; 
        }

    }

    public hide() {

        this.bannerAd?.hide() ; 

    }

    public bannerAd : any = null ; 

    public isLoaded = false ; 

}

// 插屏广告
class AdInterstitial {

    public init() {

        // @ts-ignore
        if (!tt.createInterstitialAd){
            return ;
        }

        // 创建插屏广告实例，提前初始化
        // @ts-ignore
        let interstitialAd = tt.createInterstitialAd({
            adUnitId: '80uwb2shjw11mh3efq'
        })
        this.interstitialAd = interstitialAd ; 

        interstitialAd.onLoad(() => {
            console.log('插屏 广告加载成功')
        })

        interstitialAd.onError(err => {
            console.log( "插屏 加载错误：" + err)
        })


    }

    public show() {

        let interstitialAd = this.interstitialAd ;
        if (interstitialAd) {
            interstitialAd.show().catch((err) => {
                console.error(err)
            })
        }

    }

    public interstitialAd : any ;
    
}


// 激励视频
class AdRewardedVideo {

    // 成功
    public onSuccess : ()=>void = null ; 

    // err == null 播放中途退出，不下发游戏奖励
    public onFailed : ( err : string )=>void = null ; 

    public onCancel : ()=>void ; 

    public init() {

        // 创建激励视频广告实例，提前初始化
        // @ts-ignore
        let videoAd = tt.createRewardedVideoAd({
            adUnitId: '2b831d1g4ak2dmf3j2'
        }) ;
        this.videoAd = videoAd ; 

        videoAd.onLoad(() => {
            console.log('激励视频 广告加载成功')
        })
        

        videoAd.onError(err => {
            console.log(err) ;
            this.onFailed?.( err.toString() ) ;
        })

        videoAd.onClose(res => {
            // 用户点击了【关闭广告】按钮
            // 小于 2.1.0 的基础库版本，res 是一个 undefined
            if (res && res.isEnded || res === undefined) {
                // 正常播放结束，可以下发游戏奖励
                this.onSuccess?.( ) ; 
            }
            else {
                // 播放中途退出，不下发游戏奖励
                this.onCancel?.( )
            }
        })

    }

    public show() {

        let videoAd = this.videoAd  ; 
        
        if( !videoAd ) return ; 

        // 用户触发广告后，显示激励视频广告
        videoAd.show().catch(() => {
            // 失败重试
            videoAd.load()
            .then(() => videoAd.show())
            .catch(err => {

                console.log('激励视频 广告显示失败')

                videoAd.load()
                .then(() => videoAd.show())

            })
        })

    }

    public videoAd : any ; 

}