
// 词缀、能力。表示最小的能力条目
export class Affix {

    public type = AffixType.None ; 

    // 参数 数字
    public argN : number = 0 ; 
    // 参数 数字 列表
    public argNList : Array< number > = null ;

    // 参数 字符串
    public argStr : string = null ; 
    // 参数 字符串 列表
    public argStrList : Array< number > = null ;

}

/*
    词缀类型

    属性修改方式：
        设置为指定值。
        增加指定值。Increase 表示是增加

    值的类型：
        具体值。
        百分比。Percent 表示百分比

*/
export enum AffixType {

    None = "None" , 

    // ——————————————————————————————————————————————————————————
    // 综合性 的


    // 立刻获得货币。
    GetMomeyNow = "GetMomeyNow" , 

    // 每波后，额外获得货币。
    // 具体值受合成等级影响 val *= 合成等级
    GetMomeyPerWave = "GetMomeyPerWave" , 

    // 角色 血量 增加 具体值。
    // 具体值受合成等级影响 val *= 合成等级
    HpMaxIncrease = "HpMaxIncrease" , 

    // 角色 血量 增加 百分比
    HpMaxIncreasePercent = "HpMaxIncreasePercent" , 

    // 角色 最大血量 增加 具体值 增加 百分比
    HpMaxIncreaseIncreasePercent = "HpMaxIncreaseIncreasePercent" , 

    // 角色 血量 恢复 百分比。
    // 具体值受合成等级影响 val *= 合成等级
    HpRegenIncreasePercent = "HpRegenIncreasePercent" , 

    // 角色 血量 恢复 具体值
    HpRegenIncrease = "HpRegenIncrease" , 

    // 角色 血量 回复 速度 增加 百分比
    HpRegenSpeedIncreasePercent = "HpRegenSpeedIncreasePercent" , 

    // 经验获取 增加 百分比
    ExpIncreasePercent = "ExpIncreasePercent" , 
    
    // 随机一个物件合成等级提升一级
    RndArticleMergeGradeUpgrade = "RndArticleMergeGradeUpgrade" , 

    // ——————————————————————————————————————————————————————————
    // 作用在 Tower 上的 光环

    // 光环 攻速 增加
    AuraAttackSpeedIncreasePercent = "AuraAttackSpeedIncreasePercent" , 

    // 光环 伤害 增加
    AuraAttackDamageIncreasePercent = "AuraAttackDamageIncreasePercent" , 

    // 光环 伤害 增加 全体
    AuraAttackDamageIncreaseAllPercent = "AuraAttackDamageIncreaseAllPercent" , 

    // 光环 给周围武器加暴击概率。
    AuraAttackCritProbabilityIncrease = "AuraAttackCritProbabilityIncrease" , 


    // ——————————————————————————————————————————————————————————
    // 作用在 Tower 上的

    // 攻击 伤害 增加 百分比
    AttackDamageIncreasePercent = "AttackDamageIncreasePercent" , 

    // 攻击 伤害 增加 百分比 如果击中的是满血的
    AttackDamageIncreasePercentFullHp = "AttackDamageIncreasePercentFullHp" , 

    // 攻击 速度 增加 百分比
    AttackSpeedIncreasePercent = "AttackSpeedIncreasePercent" , 

    // 攻击子弹存活时间增加百分比
    // AttackBulletLifeIncreasePercent = "AttackBulletLifeIncreasePercent" , 

    // 攻击 自身吸血 设置为 百分比
    AttackLifeStealingPercent = "AttackLifeStealingPercent" , 

    // 攻击 N 次攻击之后暴击
    AttackCritPerAttackNTimes = "AttackCritPerAttackNTimes" , 
    
    // 攻击 N 次攻击之后暴击 次数 增加
    AttackCritPerAttackNTimesIncrease = "AttackCritPerAttackNTimesIncrease" , 

    // 攻击 暴击概率 增加 百分比
    AttackCritProbabilityIncreasePercent = "AttackCritProbabilityIncreasePercent" ,  

    // 攻击 暴击 倍数 增加 百分比
    AttackCritMultipleIncreasePercent = "AttackCritMultipleIncreasePercent" ,  



    // 攻击 子弹 飞行速度 增加 百分比
    AttackBulletMoveSpeedIncreasePercent = "AttackBulletMoveSpeedIncreasePercent" , 

    // 攻击 子弹 穿透 增加
    AttackBulletPassIncrease = "AttackBulletPassIncrease" , 

    // 攻击 子弹 弹射边框次数 增加
    AttackBulletBounceRectIncrease = "AttackBulletBounceRectIncrease" , 

    // 攻击 子弹 弹射边框和敌人次数 增加
    AttackBulletBounceRectEnemyIncrease = "AttackBulletBounceRectEnemyIncrease" , 

    // 攻击 子弹 在敌人间弹射次数 增加
    AttackBulletBounceBetweenEnemiesIncrease = "AttackBulletBounceBetweenEnemiesIncrease" , 

    // 攻击 子弹 穿透增加9999,边框反弹。
    AttackBulletPassInfiniteAndBounceRectIncrease = "AttackBulletPassInfiniteAndBounceRectIncrease" , 

    // 攻击 子弹 散射 数量 增加
    AttackBulletScatterIncrease = "AttackBulletScatterIncrease" , 

    // 攻击 子弹 散射 数量 增加 概率
    AttackBulletScatterIncreaseProbability = "AttackBulletScatterIncreaseProbability" , 

    // 攻击 子弹 大小 增加 百分比
    AttackBulletSizeIncreasePercent = "AttackBulletSizeIncreasePercent" , 


    // 攻击 子弹 连发
    AttackContinuous = "AttackContinuous" , 


    // 攻击 子弹 给击中的Role添加指定Buff。
    AttackBulletAddBuff = "AttackBulletAddBuff" , 

    // 攻击 子弹 击中目标后，范围内给Role添加Buff
    AttackBulletAddBuffRadius = "AttackBulletAddBuffRadius" , 

    // 攻击 子弹 击中目标后，范围内给 Role 添加 Buff。范围 增加 百分比
    AttackBulletAddBuffRadiusIncreasePercent = "AttackBulletAddBuffRadiusIncreasePercent" , 

    // 攻击 子弹 击中目标后，范围内给 Role 添加 Buff。Buff 数值 增加 百分比
    AttackBulletAddBuffRadiusBuffIncreasePercent = "AttackBulletAddBuffRadiusBuffIncreasePercent" , 

    // 攻击 子弹 概率 给击中的Role添加指定Buff。每次击中都是一次独立的概率事件，击中后计算概率。
    AttackBulletProbabilityAddBuff = "AttackBulletProbabilityAddBuff" , 

    // 攻击 子弹 附加的中毒Buff 伤害 增加 百分比
    AttackBulletPoisoningDamageIncreasePercent = "AttackBulletPoisoningDamageIncreasePercent" , 

    // 攻击 子弹 附加的中毒Buff 持续时间 增加 百分比
    AttackBulletPoisoningDurationIncreasePercent = "AttackBulletPoisoningDurationIncreasePercent" , 

    // 攻击 子弹 爆炸 半径 增加 百分比
    AttackBulletExplodeRadiusIncreasePercent = "AttackBulletExplodeRadiusIncreasePercent" , 

    // 攻击 子弹 爆炸 伤害 增加 百分比
    AttackBulletExplodeDamageIncreasePercent = "AttackBulletExplodeDamageIncreasePercent" , 


    // 每波低于30%血量立刻回复20%血
    HpRegenIncreasePercentBelowPercentPerWave = "HpRegenIncreasePercentBelowPercentPerWave" , 


    // 合并 启用
    MergeEnable = "MergeEnable" , 

    // 重叠开启。无参数表示开启实体所有Buff的重叠。
    OverlapEnable = "OverlapEnable" , 

    // AttackBulletProbabilityAddBuff =

    // ——————————————————————————————————————————————————————————
    // 作用在 Player 上的 Buff 词缀



    // ——————————————————————————————————————————————————————————
    // 作用在 Role 身上的 Buff 词缀。

    

    // 燃烧
    Burn = "Burn" , 

    // 中毒
    Poisoning = "Poisoning" , 

    // 中毒。触发伤害是攻击力的百分比
    PoisoningPercent = "PoisoningPercent" , 

    // 冰冻
    Frozen = "Frozen" , 

    // 减速 百分比
    MoveSpeedDecreasePercent = "MoveSpeedDecreasePercent" , 

    // 眩晕
    Stun = "Stun" , 

    // 击退
    Knockback = "Knockback" , 

    // 爆炸 
    Explode = "Explode" , 
    
}