import { instantiate, Node, Size, Tween, Vec2, Vec3 } from "cc";
import { PfMgrBagArticle } from "../../../Common/PfMgr/PfMgr";
import { CfgArticle, CfgArticleMgr } from "../../../Common/Cfg/CfgArticle";
import { UiBagCell } from "../Grid/UiBagCell";
import { CfgShapeItem } from "../../../Common/Cfg/CfgShape";
import { Tower } from "../../World/1.Base/Tower/Entity/Tower";


// 背包物品实体
export class BagArticle {

    // 配置
    public cfg : CfgArticle ; 

    // 形状配置
    public cfgShape : CfgShapeItem ; 


    // 占用的Cell
    public cells = new Array< UiBagCell >() ; 

    // 等级。从1开始。
    public grade = 1 ; 

    // 合成等级。从1开始
    public gradeMerge = 1 ; 

    // 是否是扩展物件
    public isBagExpand = false ; 

    // 关联的塔对象
    public tower : Tower = null ; 

    // 表现
    public view : BagArticleView = null ; 

    
}

// 表现
export class BagArticleView {

    // 表现
    public root : Node ;

    public main : Node ; 

    // 形状原点的位置
    public posShapeCoordOrig : Vec3 ; 

    // 表现的大小
    public size : Size ; 

    // 合并提示形状
    public effectMergeHintShape : Node ; 

    // 缓动动画 合并提示
    public tweenMergeHint : Tween< Node > = null ; 

    // 看广告解锁标签
    public adTag : Node ; 

}

