import { director, game, sys, tween } from "cc";


// 时间设置器
export class DateTimeSync {

    // 重试次数
    public static count = 20 ;

    public static exe( cb : ( ok : boolean )=>void ) {

        DateTimeRequest.getFromSuNing( ( ok : boolean , date : Date )=>{

            if( ok == false ) {

                console.log( "时间同步失败,1秒后重试" ) ;

                this.count-- ; 
                if( this.count <= 0 ) {
                    cb( false ) ; 
                    return ; 
                }

                tween( this ).delay( 1 ).call( ()=>{
                    this.exe( cb ) ; 
                } ).start() ; 

                return ; 
            }

            DateTimeMgr.ins = new DateTimeMgr() ; 
            DateTimeMgr.ins.dtStart = date ; 

            cb( true ) ; 

        } ) ;

    }

}

// 时间请求
export class DateTimeRequest {

    // 从苏宁API获取
    public static getFromSuNing( cb : ( ok : boolean , date : Date )=>void ) {

        if( sys.platform == sys.Platform.WECHAT_GAME ) {
            // 微信http请求
            this.wxRequest( cb ) ;
            return ; 

        }
        else if( sys.platform == sys.Platform.BYTEDANCE_MINI_GAME )
        {
            // 抖音http请求
            this.ttRequest( cb ) ;
        }
        else {
            this.fetch( cb ) ; 
        }
        
        

    }

    // fetch 请求
    public static fetch( cb : ( ok : boolean , date : Date )=>void ) {

        /*
            http 请求：
            https://docs.cocos.com/creator/manual/zh/advanced-topics/http.html?h=http

            fetch 用法
            https://segmentfault.com/a/1190000008484070?utm_source=tag-newest
            
        */ 
        let url = "https://quan.suning.com/getSysTime.do" ; 
        fetch( url ).then((response: Response) => {
            // return response.text() ; 
            return response.json()
        }).then((value) => {
            // console.log(value);

            let dt = new Date( value[ "sysTime2" ] ) ;
            console.log( "获取服务器时间完成：" + dt.toString() ) ; 
            
            cb( true , dt ) ; 

        }).catch( ( e )=>{

            console.log( "获取服务器时间出错" ) ; 

            cb( false , null ) ; 

        } ) ;

    }

    // 微信请求
    public static wxRequest( cb : ( ok : boolean , date : Date )=>void ) {

        /*
            参考：https://developers.weixin.qq.com/miniprogram/dev/api/network/request/wx.request.html
        
        */

        let url = "https://quan.suning.com/getSysTime.do" ; 

        // @ts-ignore
        wx.request({
            url: url , 
            success (res) {
                console.log(res.data)

                let dt = new Date( res.data[ "sysTime2" ] ) ;
                cb( true , dt ) ; 
                
            } , 

            fail() {

                cb( false , null ) ; 

            }

        }) ; 

    }

    // 抖音请求
    public static ttRequest( cb : ( ok : boolean , date : Date )=>void ) {

        /*
            参考：https://developers.weixin.qq.com/miniprogram/dev/api/network/request/wx.request.html
        
        */

        let url = "https://quan.suning.com/getSysTime.do" ; 

        // @ts-ignore
        tt.request({
            url: url , 
            success (res) {
                console.log(res.data)

                let dt = new Date( res.data[ "sysTime2" ] ) ;
                cb( true , dt ) ; 
                
            } , 

            fail() {

                cb( false , null ) ; 

            }

        }) ; 

    }

}

/*
    时间管理

    使用服务器同步时间，可以防止玩家修改本机时间跳过时间间隔。
    也可换成使用本机时间。

    本类作用：屏蔽时间服务器和本地时间的使用差异。

*/ 
export class DateTimeMgr {

    public static ins : DateTimeMgr = null ; 

    // 游戏开始时的时间点
    public dtStart : Date = null ; 

    // 取得现在的时间
    public getNowDateTime() : Date {

        let n = Math.floor( game.totalTime / 1000 ) ; 
        let dt = new Date( this.dtStart ) ; 
        dt.setSeconds( this.dtStart.getSeconds() + n ) ; 
        return dt ; 

        // // 时间相加
        // let dt = new Date( this.dtStart ) ;
        // dt.setSeconds( this.dtStart.getSeconds() + 100 ) ; 

        // 总体秒数
        // let n = ( dt.valueOf() - this.dtStart.valueOf() ) / 1000 ; 

    }


}