import { CurrentMax } from "../../Game/World/1.Base/Support/SimpleStruct/CurrentMax";
import { CfgGeneral } from "../Cfg/CfgGeneral";


// 背包
export class AccountBag {

    // 物件等级
    public grade = new Map< string , number >() ; 

    // 上阵的
    public deploy = Array< string >() ; 

    // 获得的 已解锁的
    public unlocked = Array< string >() ; 
 
    // 体力值
    public stamina = new CurrentMax() ; 

}


// 主线关卡
export class AccuntLevel {

    // 打到的关卡索引。从 1 开始，显示和逻辑一致，降低认知负担。
    public levelIdx : number = 1 ; 

    // 打到的波数
    public waveIdx = 0 ; 

}

export class AccountDaySign{

    //可签到的天数
    public DaySign:number=1;
    //是否已领取
    public Received:boolean=false;
}

/**
 * 邀请好友
 * */
export class AccuntInvite{

    //是否通过邀请链接进入 0为否 1为是
    public IsInviter:number;
    //邀请玩家的id
    public InviterId:string;
    //当前邀请的次数
    public InviteCount:number=1;
    //邀请的校验码-用来判断是那一波的邀请
    public Invite_check:string;
    //已成功邀请的人数
    public Aready_InviteCount:number=0;
}

//用户数据
export class AccuntInfo{

    public Uid:number;

    public Last_login_time:string;
   
}

export class AccountMoney {

    // 货币 1级
    public money001 = 0 ; 

    // 货币 2级
    public money002 = 0 ; 

}

export class Account {

    public static ins = new Account() ; 

    public money = new AccountMoney() ; 

    public bag = new AccountBag() ; 

    public mainLevel = new AccuntLevel() ;

    public accuntInfo = new AccuntInfo();

    public accuntInvite = new AccuntInvite();

    // 拥有的物品
    public items = new Map< string , number >() ;

    // 体力
    public stamina = new CurrentMax() ; 
    
    // 波次奖励 领取记录
    public waveReward = new Map< number , Set< number > >() ; 

    public accountdaysign=new AccountDaySign();

    public constructor() {

    }

    public init() {

        this.money.money001 = 500 ; 
        this.money.money002 = 500 ; 

        // 读取配置的
        this.bag.deploy.splice( 0 , this.bag.deploy.length ) ; 
        this.bag.deploy.push( ... CfgGeneral.ins.BagInitDeployedIds ) ; 

        this.bag.unlocked.splice( 0 , this.bag.unlocked.length ) ; 
        this.bag.unlocked.push( ... CfgGeneral.ins.BagInitUnlockedIds ) ; 

        this.stamina.current = 30 ; 
        this.stamina.max = 30 ; 
        
    }



}
