import { AffixType } from "./Affix";
import { Buff } from "./Buff";


// 数据 增长的
export class DataIncrease {

    // ——————————————————————————————————————————————————————————————————————
    // 攻击类的 

    // 攻击 伤害 百分比
    public attackDamageIncreasePercent = 0 ; 

    // 攻击 速度 百分比
    public attackSpeedIncreasePercent = 0 ;

    // 攻击 暴击 概率 增加 百分比
    public attackCritProbabilityIncreasePercent = 0 ; 

    // 攻击 暴击 倍数 增加 百分比
    public attackCritMultipleIncreasePercent = 0 ; 

    // 攻击 吸血 增加 百分比
    public attackLifeStealingPercent = 0 ; 


    public AttackBulletPoisoningDamageIncreasePercent = 0 ; 
    public AttackBulletPoisoningDurationIncreasePercent = 0 ; 

    public AttackBulletExplodeRadiusIncreasePercent = 0 ; 
    public AttackBulletExplodeDamageIncreasePercent = 0 ; 

    public AttackBulletAddBuffRadiusIncreasePercent = 0 ; 
    public AttackBulletAddBuffRadiusBuffIncreasePercent = 0 ; 

    public AttackDamageIncreasePercentFullHp = 0 ; 

    public AttackBulletProbabilityAddBuff = new Map< string , number >() ;

    // ——————————————————————————————————————————————————————————————————————
    // Hp 类

    // 血量 上限 增加 具体值
    public hpMaxIncrease = 0 ; 

    // 增加的血量最大值 增加 百分比
    public hpMaxIncreaseIncreasePercent = 0 ; 

    // 血量 恢复 增加 百分比
    public hpRegenIncreasePercent = 0 ; 

    // 血量 恢复 速度 增加 百分比
    public hpRegenSpeedIncreasePercent = 0 ; 

    // ——————————————————————————————————————————————————————————————————————
    // 移动

    // 移动 速度 变化量 百分比
    public moveSpeedIncreasePercent = 0 ; 




    // 所有数值型属性重置为0。
    public reset() {

        for( let v in this ) {
            
            if( typeof this[ v ] === 'number' ){
                this[ v.toString() ] = 0 ;
            } 

        } // end for

        this.AttackBulletProbabilityAddBuff.clear() ; 

    }

}

// Buff 增量/变化量 计算
export class BuffIncreaseCalc {

    public static exeBuffs( buffs : Array<Buff> , data : DataIncrease ) {
        for( let i = 0 ; i < buffs.length ; ++i ) {
            this.exeBuff( buffs[ i ] , data ) ; 
        } // end for

    }

    private static exeBuff( buff : Buff , data : DataIncrease ) {

        let affix = buff.affix ; 
        switch( affix.type ) {

            // 攻击力
            case AffixType.AttackDamageIncreasePercent :
            case AffixType.AuraAttackDamageIncreasePercent : 
            case AffixType.AuraAttackDamageIncreaseAllPercent : 
            {
                data.attackDamageIncreasePercent += affix.argN ; 
            }
            break ; 

            // 攻速
            case AffixType.AttackSpeedIncreasePercent : 
            case AffixType.AuraAttackSpeedIncreasePercent : 
            {
                data.attackSpeedIncreasePercent += affix.argN
            }
            break ; 

            // 暴击概率
            case AffixType.AttackCritProbabilityIncreasePercent : 
            case AffixType.AuraAttackCritProbabilityIncrease : 
            {
                data.attackCritProbabilityIncreasePercent += affix.argN
            }   
            break ;

            // 暴击倍数
            case AffixType.AttackCritMultipleIncreasePercent : {
                data.attackCritMultipleIncreasePercent += affix.argN ; 
            }
            break ; 

            // 吸血 按照百分比
            case AffixType.AttackLifeStealingPercent : 
            {
                data.attackLifeStealingPercent += affix.argN ; 
                // tower.attack.lifeStealing.percent = affix.arg1 ; 
            }
            break ; 

            // 最大血量 增加 具体值
            case AffixType.HpMaxIncrease : {
                // 受等级影响
                data.hpMaxIncrease += affix.argN ; 
            }
            break ;

            // 最大血量 增加 具体值 增加 百分比
            case AffixType.HpMaxIncreaseIncreasePercent : {

                data.hpMaxIncreaseIncreasePercent += affix.argN ; 

            }
            break ;

            // 回血
            case AffixType.HpRegenIncreasePercent : {

                if( buff.isNative == false ) {
                    data.hpRegenIncreasePercent += affix.argN ; 
                }

            }
            break ; 

            case AffixType.HpRegenSpeedIncreasePercent : {

                data.hpRegenSpeedIncreasePercent += affix.argN ; 

            }
            break ; 

            // 移速
            case AffixType.MoveSpeedDecreasePercent : 
            {
                data.moveSpeedIncreasePercent += buff.affix.argN ; 
            }
            break ; 


            case AffixType.AttackBulletProbabilityAddBuff : {
                let number = data.AttackBulletProbabilityAddBuff.get( buff.affix.argStr ) ;
                if( number == null ) {
                    number = 0 ; 
                }
                number += buff.affix.argN ;
                data.AttackBulletProbabilityAddBuff.set( buff.affix.argStr , number ) ; 
            }
            break ; 

            case AffixType.AttackDamageIncreasePercentFullHp : {
                data.AttackDamageIncreasePercentFullHp += buff.affix.argN ; 
            }
            break ;

            case AffixType.AttackBulletExplodeRadiusIncreasePercent : {
                data.AttackBulletExplodeRadiusIncreasePercent += buff.affix.argN ; 
            }
            break ; 

            case AffixType.AttackBulletExplodeDamageIncreasePercent : {
                data.AttackBulletExplodeDamageIncreasePercent += buff.affix.argN ; 
            }
            break ; 

            case AffixType.AttackBulletAddBuffRadiusIncreasePercent : {
                data.AttackBulletAddBuffRadiusIncreasePercent += buff.affix.argN ; 
            }
            break ; 

            case AffixType.AttackBulletAddBuffRadiusBuffIncreasePercent : {
                data.AttackBulletAddBuffRadiusBuffIncreasePercent += buff.affix.argN ; 
            }
            break ; 

            case AffixType.AttackBulletPoisoningDamageIncreasePercent : {
                data.AttackBulletPoisoningDamageIncreasePercent += buff.affix.argN ; 
            }
            break ; 

            case AffixType.AttackBulletPoisoningDurationIncreasePercent : {
                data.AttackBulletPoisoningDurationIncreasePercent += buff.affix.argN ; 
            }
            break ;             

        }

    }


}