import { CurrentMax } from "../../Support/SimpleStruct/CurrentMax";

export class Life
{
    // 是否是活着的。
    public get isAlive() : boolean { return this.hp.current > 0 ; }

    // 是否是死亡的。
    public get isDead() : boolean { return this.hp.current <= 0 ; }

    // 血量
    public hp = new CurrentMax();

    public add( n : number ) {

        // 只能加入整数
        n = Math.floor( n ) ;

        this.hp.current += n ; 

        // 不能超过最大值
        if( this.hp.current > this.hp.max ) {
            this.hp.current = this.hp.max ; 
        }

    }

    //// 恢复
    //public Recovery recovery = new Recovery();

    // 防御
    // public Defense defense = new Defense();


}