import { CurrentMax } from "../../Support/SimpleStruct/CurrentMax";


// 攻击
export class AttackCom {

    // 锁定计数器
    public lockCount = 0 ; 

    // 子弹ID
    public bulletId : string = null;

    // 散射数量
    public scatterCount = 0 ; 

    // CD
    public cd = new CurrentMax() ;

    // 攻击力
    public damage = 1 ;

    // 暴击
    public crit = new AttackCrit() ;

    // 吸血
    public lifeStealing = new AttackLifeStealing() ; 

    // 目标选择类型
    public targeSelType = AttackTargetSelectionType.First ; 

    // 统计攻击次数
    public count = 0 ; 

}

// 吸血
export class AttackLifeStealing {

    // 吸血百分比
    public percent = 0 ; 

}

// 暴击
export class AttackCrit {

    // 概率。小数
    public probability = 0 ; 

    // 倍数
    public multiple = 0 ; 

}


// 目标选择类型
export enum AttackTargetSelectionType {

    None = "None" , 

    // 最前
    First = "First" , 

    // 随机
    Rnd = "Rnd" , 

}